.infoBox {
    /* take as much room as u can */
    flex: 1;
    cursor: pointer;
}

.infoBox:not(:last-child) {
    margin-right: 15px;
}

.infoBox__cases {
    color: #cc1034;
    font-weight: 600rem;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infoBox__total {
    color: #6c757d !important;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px;
}

.infoBox--selected {
    border-top: 10px solid greenyellow;
}

.infoBox--red {
    border-color:red;
}

.infoBox__cases--green {
    color: lightgreen !important;
}